import React, { Component } from 'react';
import FormularioBusqueda from './FormularioBusqueda';
// import { FormularioBusqueda } from './FormularioBusqueda';

import  Resultados  from './Resultados';
class Container extends React.Component {

  constructor(){
    super ();
    this.state = {
      resultados: [],
      usuario: 'gaearon',
      incluirMiembro : false
    }
  }

  componentDidMount(){
    this.buscarResultados(this.state);
  }

  buscarResultados(params){
    var url = 'https://api.github.com/users/' +  params.usuario + '/repos?sort=updated';
    if ( params.incluirMiembro){
      url += '&type=all'
    }

    fetch(url).then( function (response) {
      if (response.ok){
        response.json().then( function (body){
          this.setState ({resultados: body});
        }.bind(this))
      } else {
        this.setState({ resultados: [] })
      }
    }.bind(this))
  }

  cambiarCriterioDeBusqueda(state){
    this.setState(state);
    this.buscarResultados(state);
  }  

  render () {
      return (
          <div className="app">
              <FormularioBusqueda
              usuario = {this.state.usuario}
              incluirMiembro ={this.state.incluirMiembro}
              onBuscar={this.cambiarCriterioDeBusqueda.bind(this)}
              />
              <Resultados resultados={this.state.resultados}></Resultados>
          </div>
      )
  }

}

